body table.form-table {
	input[type="text"],
	input[type="number"] {
		height: 30px;
	}
	.repeating {
		input[type="text"],
		input[type="number"] {
			width: 100%;
		}
		td.currency_rate {
			margin: 0;
			display: flex;
			align-items: center;
		}
	}
}

table.form-table th label {
	position: relative;
	display: block;
}

table.form-table th label .woocommerce-help-tip,
table.form-table th label img.help_tip {
	position: absolute;
	top: 50%;
	right: 0;
	margin: -8px -24px 0 0;
}

.woocommerce-help-tip {
	position: relative;
	display: inline-block;
	width: 16px;;
	height: 16px;
	color: #666;
	vertical-align: middle;
	font-style: normal;
	font-size: 1.1em;
	line-height: 16px;
}

.woocommerce-help-tip::after {
	position: absolute;
	top: 0;
	left: 0;
	margin: 0;
	width: 100%;
	height: 100%;
	content: "";
	text-align: center;
	text-indent: 0;
	text-transform: none;
	font-weight: 400;
	font-variant: normal;
	font-family: Dashicons;
	line-height: 1;
	cursor: help;;
	-webkit-font-smoothing: antialiased;
	speak: none;
}
